import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LoopBackConfig, LoopBackAuth, ProjectsApi,App_usersApi } from '../../../../shared/loopback-sdk';
import * as moment from 'moment'; //for date manupalation
import {PaginatorModule} from 'primeng/primeng';
import { Lightbox, IAlbum, LightboxConfig } from 'angular2-lightbox';
import { CookieService } from 'angular2-cookie/core';

declare var hostname:any;
declare var sdkPrefix;
@Component({
  templateUrl: 'projects.component.html'
})
export class ProjectsComponent {
	public contractorId:any;
	busy:any;
	projectData:any;
	display:boolean = false;
	userList:any;
	ifEmpty:boolean = false;
	userDetails:any;
	rolename:any;
	loading:boolean = true;
	_albums:any = [];
	exportData:any = [];
	filteredUserFullName: any;
	allUsersFullName: any;
	projectStatus:any="";
	projectName:any;
	createdAt:any;
	maxDate:Date;
	contractorName:any;

	//variables for filter & pagination
	createdAtSearch:any;
	projectNameSearch:any;
	projectStatusSearch:any="";
	paginationVal:any=true;
	perPageLimit:number = 10;
	offset:number=0;
	total:number;

	constructor(private uApi:App_usersApi, private projectsApi:ProjectsApi, private route:ActivatedRoute, private _cookieService: CookieService, private router: Router, private loopAuth:LoopBackAuth,private _lightbox: Lightbox) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles[0].name;
		this.contractorId = this.route.snapshot.params['id'];
		if(this.contractorId == "all"){
			this.contractorId = 0;
		}
		this.maxDate = new Date();
	}

	ngOnInit(): void {
		this.getCount();
	}

	getCount(){
		this.loading = true;
		if(this.createdAt){
			this.createdAtSearch = moment(this.createdAt).format("YYYY-MM-DD hh:mm:ss.SSS");
		}

		var dataArrObj = {
			"contractor_id":this.contractorId,
			"project_name":this.projectNameSearch,
			"created_date":this.createdAtSearch,
			"status":this.projectStatusSearch,
			"page":this.offset,
			"limit":this.perPageLimit
		}

		this.busy = this.projectsApi.getProjectsCount(dataArrObj).subscribe(
			data=>{
				this.total = data['result'][0]['total'];
				if(this.total <= 10){
					this.paginationVal = false;
				}else{
					this.paginationVal = true;
				}
				this.getData(dataArrObj);
			},
			err=>{

			},
			()=>{

			}
		)
	}

	paginate(event) {
        this.projectData = [];
		this.offset=event.page;
		var user_id;
		if(this.rolename == "$ra"){
			user_id = this.userDetails.userDet.id;
		}else{
			this.rolename = "";
		}

		this.paginationVal=true;
		if(this.createdAt){
			this.createdAt = moment(this.createdAt).format("YYYY-MM-DD hh:mm:ss.SSS");
		}
		var dataArrObj = {
			"contractor_id":this.contractorId,
			"project_name":this.projectNameSearch,
			"created_date":this.createdAtSearch,
			"status":this.projectStatusSearch,
			"page":this.offset,
			"limit":this.perPageLimit
		}

		this.getData(dataArrObj);
	}

	searchData(){
		this.offset = 0;
		this.paginationVal=false;
		this.ifEmpty = false;
		this.projectData =[];
		this.projectNameSearch = this.projectName;
		this.createdAtSearch = this.createdAt;
		this.projectStatusSearch = this.projectStatus;
		this.getCount();
	}

	resetPage(){
		this.offset = 0;
		this.paginationVal=false;
		this.projectStatus = "";
		this.projectName = "";
		this.createdAt = "";
		this.createdAtSearch = "";
		this.projectStatusSearch = "";
		this.projectNameSearch = "";
		this.getCount();
	}

	getData(dataArrObj){
		let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		this._albums = [];
		this.busy=this.projectsApi.getProjects(dataArrObj).subscribe(
			data=>{
				this.loading = false;
				var totalData = data.result.length;
				this.ifEmpty = false;
				if(totalData == 0){
					this.ifEmpty = true;
				}

				for(var i=0; i<totalData; i++){
					if(data.result[i].status == 1){
						data.result[i].status = 'Active';
					}else{
						data.result[i].status = 'Inactive';
					}
					if(data.result[i].project_cover_photo){
						let img = data.result[i].project_cover_photo;
						data.result[i].project_cover_photo = hostname+"/api/containers/logo_image/"+img+'?access_token='+loginFlagToken;
					}
					data.result[i].created_at = moment(data.result[i].createdAt).format("DD - MMM - YYYY");
					this._albums.push(
						{
							src:data.result[i].project_cover_photo,
							thumb: data.result[i].project_cover_photo
						}
					);
				}
				this.projectData = data.result;

				console.log("projectData", this.projectData);
			},
			err=>{},
			()=>{}
		)
	}

	// filterFullName(event){ 
	// 	let query = event.query;
	// 	this.busy = this.createdApi.getCreatedBy("hpb_info_tbl",this.perPageLimit,"",query).subscribe((dataRes)=>{
	// 		this.allUsersFullName = dataRes['result'];
	// 		this.filteredUserFullName = this.filterAutocompleteData(query, this.allUsersFullName,'sph_name');
	// 	});
	// }

	// filterAutocompleteData(query, filterAllData: any[],keyName):any[] {
	// 	//in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
	// 	let filtered : any[] = [];
	// 	for(let i = 0; i < filterAllData.length; i++) {
	// 		let currData = filterAllData[i];
	// 			if(query=='' || query==null){
	// 				filtered.push(currData);
	// 				if(i==this.perPageLimit){
	// 					break;
	// 				}
	// 			}else if(currData[keyName].toString().toLowerCase().includes(query.toLowerCase())) {
	// 				  filtered.push(currData);
	// 			}
	// 	} 
	// 	return filtered;
	// }

	open(index: number): void {
		// open lightbox
		this._lightbox.open(this._albums, index);
	}
}


